.ss-dashboard-wrap-announcement {
  padding: 5px;
  cursor: pointer;

  .ss-dashboard-wrap-badge-count {
    font-size: 10px;
  }
}

.ss-dashboard-wrap-announcement-dropdown {
  position: relative;
  margin-top: 8px;
  
  .announcement-wrap {
    min-width: 300px;
    background-color: @white;
    border-radius: 8px;
    font-size: 14px;
    padding-bottom: 20px;
    overflow: hidden;
    box-shadow: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);
  }

  &::before {
    content: " ";
    position: absolute;
    top: -6px;
    right: 10px;
    width: 0; 
    height: 0; 
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-bottom: 8px solid @blue;

  }

  .ss-dashboard-wrap-dropdown-menu {
    box-shadow: none;
    border: 0;
  }

  .announcement-title {
    background-color: @blue;
    color: @white;
    padding: 8px 16px;
    
  }

  .ss-dashboard-wrap-dropdown-menu-item {
    font-size: 14px;
    padding: 8px 20px;
  }

}

.ss-dashboard-wrap-anncmnt-modal {
  .ss-dashboard-wrap-modal-close {
    color: @white;
  }
  .ss-dashboard-wrap-modal-header {
    padding: 0;
  }
}

.announcement-title {
  background-color: @gray3;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 16px 24px 16px 20px;

  h3 {
    color: @white;
    margin-bottom: 0;
  }
}

.announcement-image-links {
  a { margin: 0 6px;}
}