.ss-dashboard-app-notification {
  position: fixed;
  // top: (@main-header-height + 20px);
  // right: -500px;
  top: 50%;
  left: 50%;
  margin-top: -120px;
  margin-left: -180px;
  width: 360px;
  background-color: @white;
  border-radius: 8px;
  border: @border-color-base 1px solid;
  box-shadow: 0 0px 10px rgba(0,55,128,.2);
  transition: all 300ms ease-out;
  z-index: 1;
  opacity: 0;

  &.show {
    z-index: 999;
    right: 24px;
    opacity: 1;
  }

  @media screen and (max-width: 767px) {
    top: 56px;
  }
}

.ss-dashboard-app-notification-overlay {
  position: fixed;
  background-color: rgba(0,0,0,.5);
  top: 0;
  right: 0;
  z-index: 998;
  opacity: 0;
  transition: all 300ms ease-out;
  z-index: 1;

  &.show {
    z-index: 998;
    opacity: 1;
    bottom: 0;
    left: 0;
  }
}

.ss-dashboard-app-notification-wrap {
  padding: 16px;
  display: flex; 
}

.ss-dashboard-app-notification-icon {
  width: 40px;
  font-size: 24px;
  color: @blue;
}

.app-notification-body-title {
  font-size: 12px;
  font-weight: bold;
  margin-bottom: 16px;
  padding: 8px 0;
}

.ss-dashboard-app-notification-body {
  width: calc(100% - 40px);
  .ss-dashboard-wrap-btn-sm {
    font-size: 12px;
    height: 30px;
    min-width: 60px;
    border-radius: 4px !important;
    float: right;
  }
}

.app-notification-body-message {
  a:first-child { margin-right: 12px}
  img {
    width: 125px;
  }
}

.bell-icon {
  animation-delay: 0s;
}

.app-notification-body-footer {
  font-size: 12px;
  padding: 12px 0 8px;
}

@-webkit-keyframes tada {
  0% {
      -webkit-transform: scaleX(1);
      transform: scaleX(1)
  }

  10%,20% {
      -webkit-transform: scale3d(.9,.9,.9) rotate(-3deg);
      transform: scale3d(.9,.9,.9) rotate(-3deg)
  }

  30%,50%,70%,90% {
      -webkit-transform: scale3d(1.1,1.1,1.1) rotate(3deg);
      transform: scale3d(1.1,1.1,1.1) rotate(3deg)
  }

  40%,60%,80% {
      -webkit-transform: scale3d(1.1,1.1,1.1) rotate(-3deg);
      transform: scale3d(1.1,1.1,1.1) rotate(-3deg)
  }

  to {
      -webkit-transform: scaleX(1);
      transform: scaleX(1)
  }
}

@keyframes tada {
  0% {
      -webkit-transform: scaleX(1);
      transform: scaleX(1)
  }

  10%,20% {
      -webkit-transform: scale3d(.9,.9,.9) rotate(-6deg);
      transform: scale3d(.9,.9,.9) rotate(-6deg)
  }

  30%,50%,70%,90% {
      -webkit-transform: scale3d(1.25,1.25,1.25) rotate(6deg);
      transform: scale3d(1.25,1.25,1.25) rotate(6deg)
  }

  40%,60%,80% {
      -webkit-transform: scale3d(1.25,1.25,1.25) rotate(-6deg);
      transform: scale3d(1.25,1.25,1.25) rotate(-6deg)
  }

  to {
      -webkit-transform: scaleX(1);
      transform: scaleX(1)
  }
}

@-webkit-keyframes bounceIn {
    0%,20%,40%,60%,80%,to {
        -webkit-animation-timing-function: cubic-bezier(.215,.61,.355,1);
        animation-timing-function: cubic-bezier(.215,.61,.355,1)
    }

    0% {
        opacity: 0;
        -webkit-transform: scale3d(.3,.3,.3);
        transform: scale3d(.3,.3,.3)
    }

    20% {
        -webkit-transform: scale3d(1.1,1.1,1.1);
        transform: scale3d(1.1,1.1,1.1)
    }

    40% {
        -webkit-transform: scale3d(.9,.9,.9);
        transform: scale3d(.9,.9,.9)
    }

    60% {
        opacity: 1;
        -webkit-transform: scale3d(1.03,1.03,1.03);
        transform: scale3d(1.03,1.03,1.03)
    }

    80% {
        -webkit-transform: scale3d(.97,.97,.97);
        transform: scale3d(.97,.97,.97)
    }

    to {
        opacity: 1;
        -webkit-transform: scaleX(1);
        transform: scaleX(1)
    }
}

@keyframes bounceIn {
    0%,20%,40%,60%,80%,to {
        -webkit-animation-timing-function: cubic-bezier(.215,.61,.355,1);
        animation-timing-function: cubic-bezier(.215,.61,.355,1)
    }

    0% {
        opacity: 0;
        -webkit-transform: scale3d(.3,.3,.3);
        transform: scale3d(.3,.3,.3)
    }

    20% {
        -webkit-transform: scale3d(1.1,1.1,1.1);
        transform: scale3d(1.1,1.1,1.1)
    }

    40% {
        -webkit-transform: scale3d(.9,.9,.9);
        transform: scale3d(.9,.9,.9)
    }

    60% {
        opacity: 1;
        -webkit-transform: scale3d(1.03,1.03,1.03);
        transform: scale3d(1.03,1.03,1.03)
    }

    80% {
        -webkit-transform: scale3d(.97,.97,.97);
        transform: scale3d(.97,.97,.97)
    }

    to {
        opacity: 1;
        -webkit-transform: scaleX(1);
        transform: scaleX(1)
    }
}

.animate__bounceIn {
    -webkit-animation-duration: .75s;
    animation-duration: .75s;
    -webkit-animation-duration: .75s;
    animation-duration: .75s;
    -webkit-animation-name: bounceIn;
    animation-name: bounceIn
}

.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.animate__tada {
  -webkit-animation-name: tada;
  animation-name: tada
}